@import url('https://fonts.googleapis.com/css?family=Mina');

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Mina', sans-serif;
  color: white;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 1);

  background-color: #434343;
  height: 100%;
  margin: 0;
  padding: 0;

  background: -webkit-gradient(linear, right top, left top, from(#527350), to(#282828));

  background: -webkit-linear-gradient(right, #527350, #282828);

  background: -o-linear-gradient(right, #527350, #282828);

  background: linear-gradient(270deg, #527350, #282828);
  background-size: 600% 600%;

  -webkit-animation: background-animation 59s ease infinite;

          animation: background-animation 59s ease infinite;
}

#root {
  background-color: transparent;
  background-image: -webkit-gradient(linear, left bottom, left top, color-stop(24%, transparent), color-stop(25%, rgba(255, 255, 255, .05)), color-stop(26%, rgba(255, 255, 255, .05)), color-stop(27%, transparent), color-stop(74%, transparent), color-stop(75%, rgba(255, 255, 255, .05)), color-stop(76%, rgba(255, 255, 255, .05)), color-stop(77%, transparent), to(transparent)), -webkit-gradient(linear, left top, right top, color-stop(24%, transparent), color-stop(25%, rgba(255, 255, 255, .05)), color-stop(26%, rgba(255, 255, 255, .05)), color-stop(27%, transparent), color-stop(74%, transparent), color-stop(75%, rgba(255, 255, 255, .05)), color-stop(76%, rgba(255, 255, 255, .05)), color-stop(77%, transparent), to(transparent));
  background-image: -webkit-linear-gradient(bottom, transparent 24%, rgba(255, 255, 255, .05) 25%, rgba(255, 255, 255, .05) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .05) 75%, rgba(255, 255, 255, .05) 76%, transparent 77%, transparent), -webkit-linear-gradient(left, transparent 24%, rgba(255, 255, 255, .05) 25%, rgba(255, 255, 255, .05) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .05) 75%, rgba(255, 255, 255, .05) 76%, transparent 77%, transparent);
  background-image: -o-linear-gradient(bottom, transparent 24%, rgba(255, 255, 255, .05) 25%, rgba(255, 255, 255, .05) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .05) 75%, rgba(255, 255, 255, .05) 76%, transparent 77%, transparent), -o-linear-gradient(left, transparent 24%, rgba(255, 255, 255, .05) 25%, rgba(255, 255, 255, .05) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .05) 75%, rgba(255, 255, 255, .05) 76%, transparent 77%, transparent);
  background-image: linear-gradient(0deg, transparent 24%, rgba(255, 255, 255, .05) 25%, rgba(255, 255, 255, .05) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .05) 75%, rgba(255, 255, 255, .05) 76%, transparent 77%, transparent), linear-gradient(90deg, transparent 24%, rgba(255, 255, 255, .05) 25%, rgba(255, 255, 255, .05) 26%, transparent 27%, transparent 74%, rgba(255, 255, 255, .05) 75%, rgba(255, 255, 255, .05) 76%, transparent 77%, transparent);
  height: 100%;
  background-size: 50px 50px;
}

.container-fluid {
  padding-left: 0;
}

@media only screen and (max-width: 1000px) {
  #root {
    visibility: hidden;
  }

  #root:after {
    content: 'This game works best with a desktop resolution. We\'re not going to implement mobile or tablet :(';
    display: block;
    visibility: initial;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 10px;
    left: 20px;
  }
}



