.click-area {
  width: 400px;
  height: 400px;
  border-radius: 100%;
  background: orange;
  margin: 150px auto 0;
  z-index: 1;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.click-area:hover {
  cursor: pointer;
}



